<template>
    <!-- if compSkill loading the data spin -->
    <p v-if="isLoading"> 
        <base-spinner></base-spinner>
    </p>
  <ul class="card-content" v-else-if="!isLoading && hasLiteracy">
    <table-item
        v-for="compSkillItem in loadCompSkills"
        :key="compSkillItem.id"
        :itemEntry="compSkillItem.CompSkill"
        class="media-font-size"
        >
    </table-item>
 </ul>
</template>>

<script>
    import TableItem from "./TableItem.vue";
    export default {
        components: {
            TableItem,
        },
        data() {
            return {
                error: null,
                isLoading: false,
            }
        },
        // get the literacy from state store
        computed: {
            loadCompSkills() {
                return this.$store.getters['literacy/getLiteracy'];             
            },
            hasLiteracy() {
                return !this.isLoading && this.$store.getters['literacy/hasLiteracy'];
            },   
        }, 
    } 
</script>

